define(["qtype_coderunner/blockly/core-browser",
        "qtype_coderunner/blockly/msg/en",
        "qtype_coderunner/blockly/blocks",
        "qtype_coderunner/blockly/python"],
function(Blockly, En, BlocklyBlocks, BlocklyJS) {

'use strict';

// Include the EN Locale by default.
Blockly.setLocale(En);

Blockly.Blocks = Blockly.Blocks || {};
Object.keys(BlocklyBlocks).forEach(function (k) {
  Blockly.Blocks[k] = BlocklyBlocks[k];
});

Blockly.Python = BlocklyJS;
return Blockly;
});
